import { Component, OnInit, ViewChild } from '@angular/core';
import {AllLocation} from './newLocation';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {PriceService} from '../price.service';
import Swal from 'sweetalert2';
import {NewLocation} from './allLocation';
declare var $:any;
import { Observable, interval, Subscription, of } from 'rxjs';
import {LocationService} from '../location.service';
import { NgForm } from '@angular/forms';
import { ReCaptchaComponent } from 'angular2-recaptcha';
import {FormGroup, FormControl, Validators } from '@angular/forms';


declare var grecaptcha: any;
@Component({
  selector: 'app-add-location',
  templateUrl: './add-location.component.html',
  styleUrls: ['./add-location.component.css']
})
export class AddLocationComponent implements OnInit {
  @ViewChild('ReCaptchaComponent') captcha: ReCaptchaComponent;

  private updateSubscription: Subscription;
  allLocation: AllLocation[];
  noOfLocation: any;
  newLocation = new NewLocation();
  constructor(private router: Router, private locationService: LocationService) { }

  submitted = false;

  addNewLocation = new FormGroup({
    pharmacyName: new FormControl('', Validators.required),
    phone: new FormControl('', Validators.required),
    fax: new FormControl('', Validators.required),
    addressTwo: new FormControl(''),
    tollfree: new FormControl(''),
    email: new FormControl('', [Validators.required, Validators.email]),
    addressOne: new FormControl('', Validators.required),
    city: new FormControl('', Validators.required),
    state: new FormControl('', Validators.required),
    pincode: new FormControl('', Validators.required),
    recaptcha: new FormControl('', Validators.required),
  });


  ngOnInit() {
    this.allLocations();
  }

  handleCaptcha(event){
    console.log("EVENT =======================>", event);
    this.addNewLocation.controls.recaptcha.setValue(event);
  }



  addLocation(location){
    let token = this.captcha.getResponse();
    console.log(token);
    console.log(location);
    this.captcha.reset();
    this.locationService
    .addLocation(location)
    .subscribe(()=>{
      this.allLocations(); 
    });
    console.log(location);
    Swal.fire("", "Successfully Added", "success"); 
    this.newLocation = new NewLocation();
    $('#myModal').modal('hide');
    
  }

  allLocations(){
    this.locationService.newLocation().subscribe(
      (res: AllLocation[]) => {
        this.allLocation = res;
        this.noOfLocation = this.allLocation.length;
      })
  }

  deleteLocation(id){
    console.log();
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {
        Swal.fire(
          'Deleted!',
          'Location has been removed!',
          'success'
          )
        this.locationService
        .deleteLocation(id)
        .subscribe(() => {
          this.allLocations();
        })
      }
    })
  }
}